"use client";

import React from "react";
import { DashboardCard } from "./dashboard-card";
import {
    IconClock,
    IconTarget,
    IconFocus2,
    IconCertificate,
    IconActivity
} from "@tabler/icons-react";

// Updated colors to match new theme
const metrics = [
    {
        icon: IconClock,
        label: "Experience",
        value: "10+",
        unit: "Years",
        colorClass: "text-emerald-400",
        bgClass: "bg-emerald-500/10",
        borderClass: "border-emerald-500/20"
    },
    {
        icon: IconTarget,
        label: "Status",
        value: "Seeking",
        unit: "Opportunities",
        colorClass: "text-cyan-400",
        bgClass: "bg-cyan-500/10",
        borderClass: "border-cyan-500/20"
    },
    {
        icon: IconFocus2,
        label: "Focus",
        value: "3",
        unit: "Core Areas",
        colorClass: "text-emerald-500",
        bgClass: "bg-emerald-600/10",
        borderClass: "border-emerald-600/20"
    },
    {
        icon: IconCertificate,
        label: "Top Cert",
        value: "Security+",
        unit: "Valid: 2027",
        colorClass: "text-amber-400",
        bgClass: "bg-amber-500/10",
        borderClass: "border-amber-500/20"
    }
];

const focusAreas = [
    "Solution Architecture",
    "Cybersecurity",
    "Linux Administration"
];

export function MetricsWidget() {
    return (
        <DashboardCard title="Core Metrics" icon={<IconActivity size={18} />}>
            <div className="space-y-5">
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                    {metrics.map((metric) => (
                        <div
                            key={metric.label}
                            className={`p-4 rounded-xl border transition-all duration-300 hover:scale-[1.02] ${metric.bgClass} ${metric.borderClass}`}
                        >
                            <div className="flex items-center gap-2 mb-2">
                                <div className={`p-1.5 rounded-md bg-black/20 ${metric.colorClass}`}>
                                    <metric.icon size={16} />
                                </div>
                                <span className="text-xs text-slate-400 uppercase tracking-wider font-mono">{metric.label}</span>
                            </div>
                            <div className={`text-xl lg:text-2xl font-bold font-mono tracking-tight ${metric.colorClass}`}>
                                {metric.value}
                            </div>
                            <div className="text-[10px] lg:text-xs text-slate-500 truncate">{metric.unit}</div>
                        </div>
                    ))}
                </div>

                {/* Focus Areas */}
                <div className="pt-4 border-t border-white/5">
                    <p className="text-xs text-slate-500 uppercase tracking-wider mb-3 font-mono">Focus Areas</p>
                    <div className="flex flex-wrap gap-2">
                        {focusAreas.map((area) => (
                            <span key={area} className="px-3 py-1 rounded-md bg-white/5 text-xs text-slate-300 border border-white/10 hover:border-emerald-500/30 hover:text-emerald-400 transition-colors cursor-default">
                                {area}
                            </span>
                        ))}
                    </div>
                </div>
            </div>
        </DashboardCard>
    );
}
