"use client";

import React from "react";
import { DashboardCard } from "./dashboard-card";
import { IconCertificate, IconCheck } from "@tabler/icons-react";

const certifications = [
    {
        name: "CompTIA Security+ ce",
        issuer: "CompTIA",
        validity: "Valid until March 2027",
        status: "active",
        badge: "SEC+"
    },
    {
        name: "ISC2 Candidate",
        issuer: "ISC2",
        validity: "Valid until May 2025",
        status: "active",
        badge: "ISC2"
    }
];

const training = [
    "Internal Auditor ISO 19011:2018",
    "ISMS Awareness ISO 27001"
];

export function CertificationsWidget() {
    return (
        <DashboardCard title="Certifications & Training" icon={<IconCertificate size={18} />}>
            <div className="space-y-5">
                {/* Certifications */}
                <div className="space-y-4">
                    {certifications.map((cert) => (
                        <div
                            key={cert.name}
                            className="flex items-start gap-4 p-4 rounded-xl bg-slate-900/40 border border-white/5 hover:border-emerald-500/30 transition-all duration-300 group"
                        >
                            <div className="flex-shrink-0 w-12 h-12 rounded-lg bg-gradient-to-br from-emerald-500/10 to-cyan-500/10 border border-emerald-500/20 flex items-center justify-center group-hover:scale-105 transition-transform">
                                <span className="text-xs font-bold text-emerald-400 font-mono">{cert.badge}</span>
                            </div>
                            <div className="flex-1 min-w-0">
                                <div className="flex items-center gap-2">
                                    <h4 className="text-sm font-semibold text-slate-200 truncate group-hover:text-emerald-300 transition-colors">{cert.name}</h4>
                                    <IconCheck size={14} className="text-emerald-500 flex-shrink-0" />
                                </div>
                                <p className="text-xs text-slate-500 mt-0.5">{cert.issuer}</p>
                                <p className="text-[10px] text-emerald-500/70 font-mono mt-1 tracking-wide uppercase">{cert.validity}</p>
                            </div>
                        </div>
                    ))}
                </div>

                {/* Training */}
                <div className="pt-4 border-t border-white/5">
                    <p className="text-xs text-slate-500 uppercase tracking-wider mb-3 font-mono">Training</p>
                    <div className="space-y-2">
                        {training.map((item) => (
                            <div key={item} className="flex items-center gap-3 text-xs text-slate-400 group cursor-default">
                                <span className="text-emerald-500/50 group-hover:text-emerald-400 transition-colors">✓</span>
                                <span className="group-hover:text-slate-300 transition-colors">{item}</span>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </DashboardCard>
    );
}
