"use client";

import React from "react";
import { DashboardCard } from "./dashboard-card";
import { IconFileText } from "@tabler/icons-react";

export function SummaryWidget() {
    return (
        <DashboardCard title="Professional Summary" icon={<IconFileText size={18} />}>
            <div className="relative">
                <p className="text-slate-300 leading-relaxed text-sm font-light tracking-wide">
                    <span className="text-emerald-400 font-mono text-xs mr-2">{">"}</span>
                    Experienced IT Solution Architect and Cybersecurity Professional with over{" "}
                    <span className="text-emerald-400 font-bold">10 years</span> of expertise
                    in designing, implementing, and securing enterprise technology solutions.
                    Proven track record in{" "}
                    <span className="text-cyan-400 font-medium">VAPT</span>,{" "}
                    <span className="text-cyan-400 font-medium">infrastructure hardening</span>, and{" "}
                    <span className="text-cyan-400 font-medium">risk mitigation</span>.
                </p>
                <div className="mt-3 flex gap-2">
                    <div className="h-1 w-12 bg-emerald-500/20 rounded-full overflow-hidden">
                        <div className="h-full bg-emerald-500/50 w-full animate-pulse" />
                    </div>
                </div>
            </div>
        </DashboardCard>
    );
}
