"use client";

import React, { useState } from "react";
import { IconScan, IconShieldCheck, IconLoader2 } from "@tabler/icons-react";
import { motion, AnimatePresence } from "framer-motion";

type ScanState = "idle" | "scanning" | "complete";

export function ScanButton() {
    const [scanState, setScanState] = useState<ScanState>("idle");
    const [progress, setProgress] = useState(0);

    const runScan = () => {
        if (scanState !== "idle") return;

        setScanState("scanning");
        setProgress(0);

        // Simulate scanning progress
        const interval = setInterval(() => {
            setProgress((prev) => {
                if (prev >= 100) {
                    clearInterval(interval);
                    setScanState("complete");
                    // Reset after 5 seconds
                    setTimeout(() => {
                        setScanState("idle");
                        setProgress(0);
                    }, 5000);
                    return 100;
                }
                return prev + Math.random() * 15;
            });
        }, 200);
    };

    return (
        <div className="space-y-4">
            <motion.button
                onClick={runScan}
                disabled={scanState !== "idle"}
                whileHover={scanState === "idle" ? { scale: 1.02, boxShadow: "0 0 20px rgba(16, 185, 129, 0.3)" } : {}}
                whileTap={scanState === "idle" ? { scale: 0.98 } : {}}
                className={`
                  w-full py-4 px-6 rounded-xl 
                  flex items-center justify-center gap-3
                  font-medium font-mono text-sm tracking-wide
                  transition-colors duration-300 relative overflow-hidden
                  ${scanState === "idle"
                        ? "bg-emerald-500/10 border border-emerald-500/30 text-emerald-400 hover:bg-emerald-500/20 hover:border-emerald-500/50 cursor-pointer"
                        : scanState === "scanning"
                            ? "bg-slate-800/50 border border-slate-700 text-slate-400 cursor-wait"
                            : "bg-emerald-500/20 border border-emerald-500 text-emerald-400 cursor-default"
                    }
                `}
            >
                <AnimatePresence mode="wait">
                    {scanState === "idle" && (
                        <motion.div
                            key="idle"
                            initial={{ opacity: 0, y: 10 }}
                            animate={{ opacity: 1, y: 0 }}
                            exit={{ opacity: 0, y: -10 }}
                            className="flex items-center gap-2"
                        >
                            <IconScan size={20} />
                            <span>INITIATE SYSTEM SCAN</span>
                        </motion.div>
                    )}
                    {scanState === "scanning" && (
                        <motion.div
                            key="scanning"
                            initial={{ opacity: 0, y: 10 }}
                            animate={{ opacity: 1, y: 0 }}
                            exit={{ opacity: 0, y: -10 }}
                            className="flex items-center gap-2"
                        >
                            <IconLoader2 size={20} className="animate-spin" />
                            <span>SCANNING NETWORK...</span>
                        </motion.div>
                    )}
                    {scanState === "complete" && (
                        <motion.div
                            key="complete"
                            initial={{ opacity: 0, y: 10 }}
                            animate={{ opacity: 1, y: 0 }}
                            exit={{ opacity: 0, y: -10 }}
                            className="flex items-center gap-2"
                        >
                            <IconShieldCheck size={20} />
                            <span>SECURE CONNECTION VERIFIED</span>
                        </motion.div>
                    )}
                </AnimatePresence>

                {/* Background Scan Line Animation */}
                {scanState === "scanning" && (
                    <motion.div
                        className="absolute inset-0 bg-gradient-to-r from-transparent via-emerald-500/10 to-transparent w-1/2 skew-x-12"
                        initial={{ x: "-100%" }}
                        animate={{ x: "200%" }}
                        transition={{ repeat: Infinity, duration: 1.5, ease: "linear" }}
                    />
                )}
            </motion.button>

            {/* Progress Bar */}
            <AnimatePresence>
                {scanState === "scanning" && (
                    <motion.div
                        initial={{ height: 0, opacity: 0 }}
                        animate={{ height: "auto", opacity: 1 }}
                        exit={{ height: 0, opacity: 0 }}
                        className="space-y-2 overflow-hidden"
                    >
                        <div className="h-1.5 w-full bg-slate-800 rounded-full overflow-hidden">
                            <motion.div
                                className="h-full bg-gradient-to-r from-emerald-500 to-cyan-500 shadow-[0_0_10px_rgba(16,185,129,0.5)]"
                                initial={{ width: 0 }}
                                animate={{ width: `${Math.min(progress, 100)}%` }}
                                transition={{ type: "spring", stiffness: 50 }}
                            />
                        </div>
                        <div className="flex justify-between text-xs font-mono text-slate-500">
                            <span>ANALYZING PACKETS...</span>
                            <span className="text-emerald-500">{Math.min(Math.round(progress), 100)}%</span>
                        </div>
                    </motion.div>
                )}
            </AnimatePresence>

            {/* Result */}
            <AnimatePresence>
                {scanState === "complete" && (
                    <motion.div
                        initial={{ opacity: 0, scale: 0.95 }}
                        animate={{ opacity: 1, scale: 1 }}
                        className="p-4 rounded-xl bg-emerald-500/5 border border-emerald-500/20"
                    >
                        <div className="flex items-center gap-2 text-emerald-400 mb-2">
                            <IconShieldCheck size={18} />
                            <span className="text-sm font-bold font-mono tracking-wider">DIAGNOSTIC REPORT</span>
                        </div>
                        <div className="space-y-1 text-xs font-mono text-emerald-400/80">
                            <p>✓ No Active Threats Detected</p>
                            <p>✓ Firewall Integrity: 100%</p>
                            <motion.p
                                initial={{ opacity: 0 }}
                                animate={{ opacity: 1 }}
                                transition={{ delay: 0.5 }}
                                className="text-white font-bold mt-3 pt-2 border-t border-emerald-500/20"
                            >
                                → READY FOR DEPLOYMENT
                            </motion.p>
                        </div>
                    </motion.div>
                )}
            </AnimatePresence>
        </div>
    );
}
