"use client";

import React from "react";
import { DashboardCard } from "./dashboard-card";
import { IconHistory, IconTerminal } from "@tabler/icons-react";

const experiences = [
    {
        title: "Solution Architect",
        company: "PT Global Infotech Solution",
        period: "June 2021 - Aug 2025",
        timestamp: "[2021.06 - 2025.08]",
        highlights: [
            "Led cybersecurity solution architecture for 10+ enterprise clients (Banking, Healthcare)",
            "Managed end-to-end product lifecycle for 10+ solutions",
            "Orchestrated complex system integrations and server infrastructure"
        ]
    },
    {
        title: "IT Consultant",
        company: "Bhinneka Inti Global Data",
        period: "Feb 2020 - July 2020",
        timestamp: "[2020.02 - 2020.07]",
        highlights: [
            "Facilitated 5+ enterprise clients in achieving ISO 27001 certification",
            "Conducted comprehensive VAPT and provided mitigation strategies"
        ]
    },
    {
        title: "Senior IT Officer",
        company: "Danakoo Mitra Artha (Fintech)",
        period: "July 2019 - Jan 2020",
        timestamp: "[2019.07 - 2020.01]",
        highlights: [
            "Maintained 99.9% uptime for digital payment platform",
            "Implemented robust security hardening for financial data servers"
        ]
    },
    {
        title: "IT Specialist",
        company: "Teknologi Adhikarya Prima",
        period: "Aug 2016 - July 2019",
        timestamp: "[2016.08 - 2019.07]",
        highlights: [
            "Managed diverse IT functions: development, infrastructure support, and analysis"
        ]
    }
];

export function ExperienceLog() {
    return (
        <DashboardCard title="Experience Log" icon={<IconHistory size={18} />} noPadding>
            <div className="flex flex-col h-full">
                {/* Terminal Header */}
                <div className="flex items-center gap-2 text-xs text-slate-500 font-mono px-4 py-3 border-b border-white/5 bg-black/20">
                    <IconTerminal size={14} />
                    <span>audit_trail.log</span>
                    <span className="ml-auto opacity-50">{experiences.length} entries</span>
                </div>

                {/* Log Entries */}
                <div className="p-5 space-y-6 overflow-y-auto max-h-[400px] scrollbar-thin scrollbar-thumb-emerald-500/20 scrollbar-track-transparent pr-2">
                    {experiences.map((exp, index) => (
                        <div key={index} className="relative pl-6 border-l border-emerald-500/20 group hover:border-emerald-500/50 transition-colors">
                            {/* Timeline dot */}
                            <div className="absolute -left-[5px] top-1.5 w-2.5 h-2.5 rounded-full bg-black border border-emerald-500/50 group-hover:bg-emerald-500 group-hover:shadow-[0_0_10px_rgba(16,185,129,0.5)] transition-all" />

                            <div className="space-y-3">
                                {/* Timestamp */}
                                <div className="text-[10px] font-mono text-emerald-500/60 bg-emerald-500/5 inline-block px-1.5 py-0.5 rounded">
                                    {exp.timestamp}
                                </div>

                                {/* Title and Company */}
                                <div>
                                    <h4 className="text-sm font-bold text-slate-200 group-hover:text-emerald-300 transition-colors">
                                        {exp.title}
                                    </h4>
                                    <p className="text-xs text-slate-500 mt-0.5">@ {exp.company}</p>
                                </div>

                                {/* Highlights */}
                                <ul className="space-y-1.5">
                                    {exp.highlights.map((highlight, hIndex) => (
                                        <li key={hIndex} className="text-xs text-slate-400 flex items-start gap-2 leading-relaxed">
                                            <span className="text-emerald-500/50 mt-1">›</span>
                                            <span>{highlight}</span>
                                        </li>
                                    ))}
                                </ul>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </DashboardCard>
    );
}
