"use client";

import React from "react";
import { motion } from "framer-motion";

interface DashboardCardProps {
    title?: string;
    icon?: React.ReactNode;
    children: React.ReactNode;
    className?: string;
    headerRight?: React.ReactNode;
    noPadding?: boolean;
}

export function DashboardCard({
    title,
    icon,
    children,
    className = "",
    headerRight,
    noPadding = false,
}: DashboardCardProps) {
    return (
        <motion.div
            whileHover={{ y: -4, transition: { duration: 0.2 } }}
            className={`glass-panel rounded-xl overflow-hidden relative group ${className}`}
        >
            {/* Animated Gradient Border */}
            <div className="absolute inset-0 bg-gradient-to-r from-emerald-500/20 via-cyan-500/20 to-emerald-500/20 opacity-0 group-hover:opacity-100 transition-opacity duration-500 -z-10" />

            {title && (
                <div className="flex items-center justify-between px-5 py-4 border-b border-white/5 bg-white/[0.02]">
                    <div className="flex items-center gap-3">
                        {icon && (
                            <span className="text-emerald-400 drop-shadow-[0_0_8px_rgba(52,211,153,0.5)]">
                                {icon}
                            </span>
                        )}
                        <h3 className="text-sm font-semibold text-slate-200 tracking-wide font-mono">
                            {title}
                        </h3>
                    </div>
                    {headerRight && (
                        <div>{headerRight}</div>
                    )}
                </div>
            )}

            <div className={`relative z-10 ${noPadding ? "" : "p-5"}`}>
                {children}
            </div>

            {/* Glow Effect on Hover */}
            <div className="absolute -inset-1 bg-gradient-to-r from-emerald-500/0 via-emerald-500/10 to-emerald-500/0 blur-xl opacity-0 group-hover:opacity-100 transition-opacity duration-500 pointer-events-none" />
        </motion.div>
    );
}
