"use client";

import React from "react";
import { DashboardCard } from "./dashboard-card";
import { IconCode } from "@tabler/icons-react";
import { motion } from "framer-motion";

const skillCategories = [
    {
        name: "Cybersecurity",
        skills: [
            { name: "Penetration Testing", level: 90 },
            { name: "Vulnerability Assessment", level: 88 },
            { name: "ISO 27001 Compliance", level: 85 },
            { name: "Server Hardening", level: 92 },
            { name: "Risk Assessment", level: 87 }
        ]
    },
    {
        name: "Infrastructure",
        skills: [
            { name: "Linux System Admin", level: 95 },
            { name: "Windows Server", level: 80 },
            { name: "Network Security", level: 88 },
            { name: "Cloud Security", level: 82 },
            { name: "Disaster Recovery", level: 85 }
        ]
    },
    {
        name: "Dev/Architecture",
        skills: [
            { name: "Solution Architecture", level: 90 },
            { name: "API Integration", level: 85 },
            { name: "DevSecOps", level: 78 },
            { name: "Python/Bash Scripting", level: 88 }
        ]
    }
];

function ProgressBar({ value, label, delay }: { value: number; label: string, delay: number }) {
    return (
        <div className="group">
            <div className="flex justify-between text-xs mb-1.5">
                <span className="text-slate-400 group-hover:text-emerald-400 transition-colors duration-300">{label}</span>
                <span className="text-emerald-500 font-mono text-[10px]">{value}%</span>
            </div>
            <div className="h-1.5 w-full bg-slate-800/50 rounded-full overflow-hidden">
                <motion.div
                    className="h-full bg-gradient-to-r from-emerald-600 to-cyan-500 rounded-full shadow-[0_0_8px_rgba(16,185,129,0.4)]"
                    initial={{ width: 0 }}
                    whileInView={{ width: `${value}%` }}
                    viewport={{ once: true }}
                    transition={{ duration: 1, delay: delay * 0.1, ease: "easeOut" }}
                />
            </div>
        </div>
    );
}

export function SkillMatrix() {
    return (
        <DashboardCard title="Skill Matrix" icon={<IconCode size={18} />}>
            <div className="space-y-6">
                {skillCategories.map((category, catIndex) => (
                    <div key={category.name}>
                        <h4 className="text-xs text-slate-500 uppercase tracking-wider mb-4 flex items-center gap-2 font-mono">
                            <span className="w-1.5 h-1.5 bg-emerald-500 rounded-full shadow-[0_0_5px_rgba(16,185,129,0.5)]" />
                            {category.name}
                        </h4>
                        <div className="space-y-4">
                            {category.skills.map((skill, index) => (
                                <ProgressBar
                                    key={skill.name}
                                    value={skill.level}
                                    label={skill.name}
                                    delay={catIndex * 2 + index}
                                />
                            ))}
                        </div>
                    </div>
                ))}
            </div>
        </DashboardCard>
    );
}
