"use client";

import React from "react";
import { DashboardCard } from "./dashboard-card";
import { StatusIndicator } from "./status-indicator";
import {
    IconMail,
    IconMapPin,
    IconBrandLinkedin,
    IconBrandGithub,
    IconWorld,
    IconAward,
    IconUser
} from "@tabler/icons-react";

const contactLinks = [
    { icon: IconMail, label: "Email", value: "attila.iqbal@gmail.com", href: "mailto:attila.iqbal@gmail.com" },
    { icon: IconMapPin, label: "Location", value: "Indonesia", href: null },
];

const socialLinks = [
    { icon: IconBrandLinkedin, label: "LinkedIn", href: "https://linkedin.com/in/iqbalattila" },
    { icon: IconWorld, label: "Portfolio", href: "https://me.kcmon.id" },
    { icon: IconBrandGithub, label: "GitHub", href: "https://github.com/aikazu" },
    { icon: IconAward, label: "Credly", href: "https://credly.com/users/iqbal-attila" },
];

export function ProfileWidget() {
    return (
        <DashboardCard className="h-full" icon={<IconUser size={18} />} title="User Identity">
            <div className="space-y-6">
                {/* Name and Title */}
                <div className="text-center pb-4 border-b border-white/5">
                    <div className="w-20 h-20 mx-auto bg-gradient-to-br from-emerald-500/20 to-cyan-500/20 rounded-full flex items-center justify-center mb-4 ring-2 ring-emerald-500/20 shadow-[0_0_20px_rgba(16,185,129,0.2)]">
                        <span className="text-2xl font-bold text-emerald-400">IA</span>
                    </div>
                    <h1 className="text-2xl font-bold text-white mb-1 tracking-tight">
                        Iqbal Attila
                    </h1>
                    <p className="text-sm text-slate-400 mb-3 font-medium">
                        Cybersecurity Solution Architect
                    </p>
                    <div className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-emerald-500/10 border border-emerald-500/20">
                        <span className="text-xs font-mono text-emerald-400">CompTIA Security+ Certified</span>
                    </div>
                </div>

                {/* Status */}
                <div className="flex items-center justify-between py-3 px-4 rounded-lg bg-black/20 border border-white/5">
                    <StatusIndicator status="online" label="Operational" size="sm" />
                    <span className="text-xs font-mono px-2 py-1 rounded bg-emerald-500/10 text-emerald-400 border border-emerald-500/20">
                        OPEN TO WORK
                    </span>
                </div>

                {/* Contact Info */}
                <div className="space-y-3">
                    <p className="text-xs text-slate-500 uppercase tracking-wider font-mono">Contact Nodes</p>
                    {contactLinks.map((contact) => (
                        <div key={contact.label} className="flex items-center gap-3 text-sm group">
                            <div className="p-1.5 rounded bg-white/5 group-hover:bg-emerald-500/10 transition-colors">
                                <contact.icon size={16} className="text-slate-400 group-hover:text-emerald-400" />
                            </div>
                            {contact.href ? (
                                <a
                                    href={contact.href}
                                    className="text-slate-300 hover:text-emerald-400 transition-colors truncate font-medium"
                                >
                                    {contact.value}
                                </a>
                            ) : (
                                <span className="text-slate-300">{contact.value}</span>
                            )}
                        </div>
                    ))}
                </div>

                {/* Social Links */}
                <div className="space-y-3">
                    <p className="text-xs text-slate-500 uppercase tracking-wider font-mono">Network Uplinks</p>
                    <div className="grid grid-cols-2 gap-3">
                        {socialLinks.map((link) => (
                            <a
                                key={link.label}
                                href={link.href}
                                target="_blank"
                                rel="noopener noreferrer"
                                className="flex items-center gap-2 text-sm text-slate-400 hover:text-white hover:bg-white/5 transition-all p-2 rounded-lg border border-transparent hover:border-white/10"
                            >
                                <link.icon size={16} className="text-emerald-500/70" />
                                <span>{link.label}</span>
                            </a>
                        ))}
                    </div>
                </div>
            </div>
        </DashboardCard>
    );
}
