"use client";

import React, { useState } from "react";
import { DashboardCard } from "./dashboard-card";
import { IconTerminal2, IconCopy, IconCheck } from "@tabler/icons-react";

const contactPlainText = `Iqbal Attila
Email: attila.iqbal@gmail.com
Location: Indonesia
LinkedIn: linkedin.com/in/iqbalattila
GitHub: github.com/aikazu
Portfolio: me.kcmon.id`;

export function CopyTerminal() {
    const [copied, setCopied] = useState(false);

    const copyToClipboard = async () => {
        try {
            await navigator.clipboard.writeText(contactPlainText);
            setCopied(true);
            setTimeout(() => setCopied(false), 2000);
        } catch (err) {
            console.error("Failed to copy:", err);
        }
    };

    return (
        <DashboardCard
            title="Terminal"
            icon={<IconTerminal2 size={18} />}
            headerRight={
                <button
                    onClick={copyToClipboard}
                    className="flex items-center gap-1.5 text-xs px-2.5 py-1.5 rounded-md bg-white/5 hover:bg-emerald-500/20 border border-white/5 hover:border-emerald-500/50 text-slate-400 hover:text-emerald-400 transition-all duration-300"
                >
                    {copied ? (
                        <>
                            <IconCheck size={12} />
                            <span>Copied!</span>
                        </>
                    ) : (
                        <>
                            <IconCopy size={12} />
                            <span>Copy</span>
                        </>
                    )}
                </button>
            }
        >
            <div className="bg-[#0a0e17] rounded-lg p-5 font-mono text-sm overflow-hidden border border-white/5 relative">
                {/* Scanline overlay */}
                <div className="absolute inset-0 bg-repeat-y opacity-[0.03] pointer-events-none bg-[url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAADCAYAAABS3WWCAAAAF0lEQVQYV2NkYGD4z8DAwMgAI0AMDAwAI3kBA4Gw1SgAAAAASUVORK5CYII=')] bg-[length:1px_3px]" />

                <div className="flex items-center gap-2 text-slate-600 mb-3 border-b border-white/5 pb-2">
                    <span className="text-emerald-500 font-bold">$</span> cat contact_info.json
                </div>
                <div className="space-y-1.5 relative z-10">
                    <div className="grid grid-cols-[80px_1fr] sm:grid-cols-[100px_1fr] gap-2">
                        <span className="text-slate-500">Name:</span>
                        <span className="text-slate-200">Iqbal Attila</span>
                    </div>
                    <div className="grid grid-cols-[80px_1fr] sm:grid-cols-[100px_1fr] gap-2">
                        <span className="text-slate-500">Role:</span>
                        <span className="text-emerald-400 font-semibold">Cybersecurity Solution Architect</span>
                    </div>
                    <div className="grid grid-cols-[80px_1fr] sm:grid-cols-[100px_1fr] gap-2">
                        <span className="text-slate-500">Email:</span>
                        <a href="mailto:attila.iqbal@gmail.com" className="text-slate-300 hover:text-emerald-400 transition-colors truncate underline decoration-dotted underline-offset-4">attila.iqbal@gmail.com</a>
                    </div>
                    <div className="grid grid-cols-[80px_1fr] sm:grid-cols-[100px_1fr] gap-2">
                        <span className="text-slate-500">Location:</span>
                        <span className="text-slate-300">Indonesia</span>
                    </div>
                    <div className="grid grid-cols-[80px_1fr] sm:grid-cols-[100px_1fr] gap-2">
                        <span className="text-slate-500">Web:</span>
                        <a href="https://me.kcmon.id" target="_blank" rel="noopener noreferrer" className="text-slate-300 hover:text-emerald-400 transition-colors underline decoration-dotted underline-offset-4">me.kcmon.id</a>
                    </div>
                    <div className="grid grid-cols-[80px_1fr] sm:grid-cols-[100px_1fr] gap-2">
                        <span className="text-slate-500">Status:</span>
                        <span className="text-emerald-500 flex items-center gap-2">
                            <span className="relative flex h-2 w-2">
                                <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-emerald-400 opacity-75"></span>
                                <span className="relative inline-flex rounded-full h-2 w-2 bg-emerald-500"></span>
                            </span>
                            Online
                        </span>
                    </div>
                </div>
                <div className="mt-4 flex items-center gap-2 text-slate-500">
                    <span className="text-emerald-500 font-bold">$</span> <span className="animate-pulse w-2 h-4 bg-emerald-500/50 block"></span>
                </div>
            </div>
        </DashboardCard>
    );
}
